import Vue from 'vue'
import Router from 'vue-router'
import Index from "@/views/index/Index";
import Data from "@/views/data/Data";
import Donation from "@/views/donation/Donation";
import Demand from "@/views/demand/Demand";
import About from "@/views/about/About";
import Perfile from "@/views/perfile/Perfile";
import PerDon from "@/views/perfile/child/PerDon";
import PerDem from "@/views/perfile/child/PerDem";
import PerNFT from "@/views/perfile/child/nft/PerNFT";
import Vote from "@/views/vote/Vote";
import IpfsInfo from "./views/ipfs/IpfsInfo";


Vue.use(Router)

const router = new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      redirect: '/index',
    },
    {
      path: '/index',
      component: Index,
      children: [
        {
          path: '/',
          redirect: '/data',
        },
        {
          path: '/data',
          component: Data,
          meta: {
            title: ""
          },
        },
        {
          path: '/donation',
          component: Donation,
          meta: {
            title: ""
          },

        },
        {
          path: '/demand',
          component: Demand,
          meta: {
            title: ""
          },
        },
        {
          path: '/vote',
          component: Vote,
          meta: {
            title: ""
          },
        },
        {
          path: '/about',
          component: About,
          meta: {
            title: ""
          },
        },
        {
          path: '/upload',
          component: IpfsInfo,

        }
      ]
    },
    {
      path: '/perfile',
      component: Perfile,
      children: [
        {
          path: '/',
          redirect: '/perfile/donation',
        },
        {
          path: '/perfile/donation',
          component: PerDon,
          meta: {
            title: ""
          },
        },
        {
          path: '/perfile/demand',
          component: PerDem,
          meta: {
            title: ""
          },
        },
        {
          path: '/perfile/nft',
          component: PerNFT,
          meta: {
            title: "NFT"
          },
        },
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})
